/* --- GENELLER --- */
ul {
    list-style: none;
}
a {
    text-decoration: none;
    color: #fff;
}

body {
    background-color: #333;
}
nav {
    color: var(--color);
}


.nav-list {
    display: flex;
    margin: auto;
    /* margin-top: 15px; */
}
.nav-item {
    font-size: 17px;
    display: block;
    /* margin-top: 12px ; */
    width: 100%;
    height: 100%;
    align-items: center;
    justify-content: center;
}
.nav-item:first-child .nav-link {
    border-bottom-left-radius: 5px;
}
.nav-item:last-child .nav-link {
    border-bottom-right-radius: 5px;
}
.nav-link {
    color: var(--color);
    display: flex;
    flex-direction: column;
    padding: 15px 40px;
    font-size: 15px;
    background-color: #414041;
    position: relative;
}
.icons {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
i.right {
    margin-left: 10px;
    align-self: center;
}
i.kurumsal {
    margin-right: 5px;
}
i.ref {
    position: relative;
    top: 1px;
}
.border-bottom {
    width: 0;
    height: 1px;
    margin: auto;
    background-color: #fff;
}
.nav-link:hover .border-bottom {
    width: 75%;
    transition: all .3s;
}
